*, *::before, *::after {
  box-sizing: border-box;
}

body {
  position: relative;
  min-height: 500px;
  max-height: 100vh;
  padding: 0;
  margin: 0;
  overflow-x: auto;
}

.popup-container {
  position: relative;
  width: 450px;
  min-height: 100vh;
  font-family: "Nunito", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  color: #364049;
  border-radius: 6.5px;
  background-color: #e3ebf2;
  padding-bottom: 85px;
}

.popup-container .popup-body .Panel {
  display: none;
}

.popup-container .popup-body .Panel[data-panel="active"] {
  display: block;
}

.Preload__Screen {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #e3ebf2;
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transition: opacity .2s ease-in-out;
}

.Preload__Screen .Preload__Spinner {
  width: 150px;
  height: auto;
  user-select: none;
}

.Preload__Screen.is-shown {
  opacity: 1;
  visibility: visible;
  transition: opacity .2s ease-in-out;
}

.Popup__Header {
  padding: 26px;
}

.Popup__Header .Header__Title {
  font-size: 32px;
  font-weight: 800;
  letter-spacing: 0.3px;
  line-height: 1;
  margin: 0;
  margin-bottom: 10px;
}

.Popup__Header .Header__Subtitle-wrapper {
  display: flex;
  align-items: baseline;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.Popup__Header .Header__Subtitle {
  font-size: 21px;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.4px;
  margin: 0;
  margin-bottom: 10px;
}

.Popup__Header .Header__Subtitle small {
  font-size: 12px;
  font-weight: 300;
  font-style: italic;
  line-height: 1;
  letter-spacing: 0.3px;
  color: #879aab;
  margin-left: 10px;
}

.Popup__Header .Header__Subtitle--subdued {
  font-size: 18px;
  font-style: italic;
  color: #879aab;
}

.Alert {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: calc(100% - 40px);
  border-radius: 5px;
  border: 3px solid #383c8c;
  background-color: #4247a6;
  padding: 25px 15px;
  margin: 45px auto 0;
  user-select: none;
  box-shadow: inset 0px 0px 5px 0px #383c8c;
}

.Alert .Alert__Icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  flex: 0 0 30px;
  margin-right: 15px;
  color: #b3b8ff;
}

.Alert .Alert__Icon-container .Alert__Icon {
  width: 100%;
  height: 100%;
}

.Alert .Alert__Content {
  flex: 1 1 100%;
}

.Alert .Alert__Content .Alert__Message {
  font-size: 18px;
  color: #b3b8ff;
  margin: 0;
}

.Accordion__Container {
  margin-bottom: 5px;
}

.Accordion__Container:last-child {
  margin-bottom: 0;
}

.Accordion__Container .Accordion__Header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 50px;
  background-color: #eff3f6;
  transition: background-color .2s ease-in;
  padding-right: 10px;
  cursor: pointer;
  user-select: none;
}

.Accordion__Container .Accordion__Header .Accordion__Icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #4d52bf;
  margin-right: 10px;
  height: 50px;
  max-width: 50px;
  flex: 0 0 50px;
  color: #fff;
}

.Accordion__Container .Accordion__Header .Accordion__Icon-container .Accordion__Icon {
  height: 30px;
  width: auto;
  fill: #fff;
}

.Accordion__Container .Accordion__Header .Accordion__Icon-container .Accordion__Icon path {
  fill: inherit;
}

.Accordion__Container .Accordion__Header .Accordion__Title {
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 0.4px;
  color: #4247a6;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex: 0 1 100%;
  padding-right: 10px;
}

.Accordion__Container .Accordion__Header .Accordion__Title a {
  color: inherit;
  text-decoration: none;
}

.Accordion__Container .Accordion__Header .Accordion__Link {
  letter-spacing: 0.3px;
  text-align: right;
  transition: .2s ease-in;
}

.Accordion__Container .Accordion__Header .Accordion__Link a {
  color: inherit;
  text-decoration: none;
}

.Accordion__Container .Accordion__Header .Accordion__Link:hover {
  color: #4d52bf;
}

.Accordion__Container .Accordion__Header.is-active, .Accordion__Container .Accordion__Header:hover {
  background-color: #dbe0eb;
}

.Accordion__Container .Accordion__Body {
  display: none;
  padding: 10px 15px;
  background-color: #f5f7fa;
}

.Accordion__Container .Accordion__Body .Accordion__Detail, .Accordion__Container .Accordion__Body .Accordion__Footer .Footer__Link, .Accordion__Container .Accordion__Footer .Accordion__Body .Footer__Link {
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.3px;
  margin-top: 0;
  margin-bottom: 5px;
}

.Accordion__Container .Accordion__Body .Accordion__Detail .Accordion__Label, .Accordion__Container .Accordion__Body .Accordion__Footer .Footer__Link .Accordion__Label, .Accordion__Container .Accordion__Footer .Accordion__Body .Footer__Link .Accordion__Label {
  color: #364049;
}

.Accordion__Container .Accordion__Body .Accordion__Detail .Accordion__Value, .Accordion__Container .Accordion__Body .Accordion__Footer .Footer__Link .Accordion__Value, .Accordion__Container .Accordion__Footer .Accordion__Body .Footer__Link .Accordion__Value {
  color: #879aab;
}

.Accordion__Container .Accordion__Body .Accordion__Detail:last-child, .Accordion__Container .Accordion__Body .Accordion__Footer .Footer__Link:last-child, .Accordion__Container .Accordion__Footer .Accordion__Body .Footer__Link:last-child {
  margin-bottom: 0;
}

.Accordion__Container .Accordion__Footer {
  display: flex;
  align-items: baseline;
  justify-content: flex-start;
  margin-top: 10px;
}

.Accordion__Container .Accordion__Footer .Footer__Link {
  margin-right: 10px;
}

.Accordion__Container .Accordion__Footer .Footer__Link a {
  text-decoration: none;
  color: #4d52bf;
  transition: color .2s ease-in;
}

.Accordion__Container .Accordion__Footer .Footer__Link a:hover {
  color: #383c8c;
}

.Accordion__Container .Accordion__Footer .Footer__Link:last-child {
  margin-right: 0;
}

.Popup__Footer {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  box-shadow: 0 -1.5px 3.5px 0px rgba(51, 51, 51, 0.3);
  background-color: #364049;
}

.Popup__Footer .Nav__List {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 0;
  padding: 15px 10px;
}

.Popup__Footer .Nav__List .List__Item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20%;
  margin: 0 10px;
}

.Popup__Footer .Nav__List .Nav__Button {
  height: 50px;
  padding: 0;
  color: #879aab;
  background: none;
  appearance: none;
  cursor: pointer;
  border: 0;
  transition: .2s ease-in;
  box-sizing: content-box;
}

.Popup__Footer .Nav__List .Nav__Button svg {
  display: block;
  width: 30px;
  margin: 0 auto 5px;
  fill: currentColor;
  transition: fill .2s ease-in;
}

.Popup__Footer .Nav__List .Nav__Button .Button__Label {
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
}

.Popup__Footer .Nav__List .Nav__Button svg, .Popup__Footer .Nav__List .Nav__Button .Button__Label {
  filter: drop-shadow(0 0px 5px rgba(0, 0, 0, 0.75));
}

.Popup__Footer .Nav__List .Nav__Button:hover {
  color: #dbe0eb;
}

.Popup__Footer .Nav__List .Nav__Button:focus {
  outline: 0;
}

.Popup__Footer .Nav__List .Nav__Button.Nav__Button--active {
  color: #eff3f6;
  transform: translateY(-2px);
}

.Popup__Footer .Nav__List .Nav__Button.Nav__Button--active svg, .Popup__Footer .Nav__List .Nav__Button.Nav__Button--active .Button__Label {
  filter: drop-shadow(0 5px 3px rgba(0, 0, 0, 0.75));
}
